.snippet{
    width: 440px;
    margin: 10px auto;
    color:black;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border: 1px solid #c4c4c4;
    text-align: left;
    background-color: white;
}
.snippet p{
    font-size: 12px;
}
.postImage{
    width: 440px;
    height: 440px;
}


.profilePic{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: auto 0px;
}
.postHead{
    padding: 10px;
}
.authorName{
    margin: 0px;
}
.postLocation{
    margin: 0px;
}
.authorAndLocation{
    margin: auto 0px auto 10px;
    }

.caption{
    margin: auto auto auto 5px; 
}

.commentor{
    margin: auto 0px;
}
.commentBody{
    margin: auto auto auto 5px;
}

.horizontalFlex{
    display: flex;
    justify-content: flex-start;
}
.verticalFlex{
    display: flex;
    flex-direction: column;
}



.likeBtn{
    outline: none;
    background-color: white;
    border: none;
}
.inputComment{
    padding: 10px;
    border-top: 1px solid #c4c4c4;
    margin-top: 10px;
}
.inputCommentField{
    width: 100%;
    border: none;
    outline: none;
    
}
.addBtn{
    outline: none;
    background-color: white;
    border: none;
}

.postTail{
    padding-right: 10px;
    padding-left: 10px;
}
.pointer{
    cursor: pointer;
}
.likesNumber{
    margin-top: 0;
}
.postComments{
    max-height: 50px;
    overflow: overlay;
}