.navbarWrapper{
    display: flex;
    justify-content: center;
    background-color: white;
    border-bottom: 1px #e9e4e4 solid;
}

.navbar{
    margin: auto;
    width: 75%;
    display: flex;
    justify-content: space-between;
    background-color: white;
}

.brand{
    font-family: 'Cookie', cursive;
    font-size: 30px;
    text-decoration: none;
    padding: 10px;
    color: #2d4059;
}
.nav{
    margin: auto 0px;
    display: flex;
    align-items: center;
}
.navBtn{
    background-color:white ;
    font-size: 16px;
    color: #2d4059;
    cursor: pointer;
    text-decoration: none;
    margin: auto 10px;
}


.circleImage{
    width: 22px;
    height: 22px;
    border-radius: 50%;
}