
.profileWrapper{
    background-color: rgba(var(--b3f,250,250,250),1);

}
.profile{
    text-align: center;
    width: 75%;
    margin: auto;
}
.horizontalFlex{
    display: flex;
    justify-content: center;
    align-items: center;
}
.verticalFlex{
    display: flex;
    flex-direction: column;
}
.head{
    padding-top: 30px;
}

.profilePic{
    margin: auto 20px auto 0px;
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.headRight{
    margin: auto 0px auto 20px;
    text-align: left;
}

.headRightItem{
    justify-content: space-between;
    width: 280px;
}

.headRightItemChild{
    font-weight: 600;
}
.userFLName{
    font-weight: 600;
    margin: 0;
}
.userEditProfile{
   justify-content: space-between;
}

.tilesPanel{
display: flex;
flex-wrap: wrap;
}
.addPostBtn{
    width: fit-content;
    margin: auto !important; 
}

.userName{
    font-weight: lighter;
    font-size: 24px;
    margin: 0;
}