.accountWrapper{
    background-color: rgba(var(--b3f,250,250,250),1);
}

.account{
    text-align: center;
    width: 75%;
    margin: auto;
    
}

.head{
    display: flex;
    justify-content: center;
    padding-top: 30px;
}

.profilePic{
    margin: auto 20px auto 0px;
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.headRight{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto 0px auto 20px;
    text-align: left;
}

.headRightItem{
    display: flex;
    justify-content: space-around;
}

.headRightItemChild{
    margin: auto 10px
}

.userEditProfile{
    display: flex;
    justify-content: space-between;
}

.tilesPanel{
display: flex;
flex-wrap: wrap;
}

.followingBtn{
    border: none;
    outline: none;
    background-color: rgba(var(--b3f,250,250,250),1);
    cursor: pointer;
    border: 1px solid #c4c4c4;
}

.userName{
    font-weight: lighter;
    font-size: 24px;
    margin: 0;
}