.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    background-color: rgba(var(--b3f,250,250,250),1);
}
/* .signinImage{
    margin: auto 10px;
    border-radius: 8px;
} */
.signinForm{
    margin: 10px auto;
    padding: 10px;
    width: 300px;
    text-align: center;
    border: 1px solid #c4c4c4;
    background-color: white;
}

.formTitle{
    font-family: 'Cookie', cursive;
    font-size: 34px;
}
.loginInput{
    border: 1px solid #c4c4c4;
    background-color: rgba(var(--b3f,250,250,250),1);
    width: 250px;
    height: 30px;
    border-radius: 4px;
    outline: none;
    margin-bottom: 5px;
    padding: 0px 0px 0px 5px;
}
.loginBtn{
    border: none;
    outline: none;
    background-color: rgb(84, 130, 228) ;
    cursor: pointer;
    color: white;
    width: 255px;
    height: 35px;
    margin: 5px auto;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 700;
}