
.horizontalFlex{
    display: flex;
    justify-content: flex-start;
}
.verticalFlex{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.authorDetails{
    justify-content: space-between;
    border-bottom: 1px #c7c1c1 solid;
}

.singlePost{
    justify-content: center;
    background-color: white;
    border: 1px #c7c1c1 solid;
    width: 800px;
    margin: auto;
}
.postComments{
    justify-content: space-between;
}


.profilePicCircle{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: auto 0px;
    cursor: pointer;
}

.postInfo{
    justify-content: space-between;
    margin: auto 20px;
    width: 300px;
    height: 500px;
}

.followBtn{
    margin: auto 0px;
    outline: none;
    border: none;
    background-color: white;
    cursor: pointer;
}

.addComment{
    justify-content: space-between;
}
.likes{
    border-top: 1px #c4c4c4 solid;
    justify-content: space-between;
}
.likeBtn{
    width: fit-content;
}
.likesNum{
    margin: 0;
}

.profileClick{
    display: flex;
    justify-content: flex-start;
}

.postImage{
    width: 500px;
    height: 500px;
}

.singlePostWrapper{
    background-color: rgba(var(--b3f,250,250,250),1);
    padding: 30px;
}
.captionWriter{
    cursor: pointer;
    font-weight: 700;
}
.authorName{
    cursor: pointer;
    font-weight: 700;
    margin-left: 10px;
}
.addComment{
    padding: 10px;
    border-top: 1px solid #c4c4c4;
    margin-top: 10px;
}
.inputCommentField{
    width: 100%;
    outline: none;
    border: none;
}

.postBtn{
    outline: none;
    border: none;
    background-color: white;
}
.commentor{
    margin: auto 0px;
}
.commentBody{
    margin: auto auto auto 5px;
}

.singlePost p{
    font-size: 12px;
}
.pointer{
    cursor: pointer;
}
.caption{
    margin: auto auto auto 5px; 
}
.commentsArray{
    max-height: 264px;
    overflow: overlay;
}